.app-booking {
  background: url(../../img/homepage/ready-bg-mobile.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 328px;
  @include for-normal-layout {
    background: url(../../img/homepage/ready-bg-desktop.jpg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 240px;
  }

  .content {
    height: 100%;
    flex-direction: column;
    justify-content: center;
    @include for-normal-layout {
      justify-content: space-between;
      flex-direction: row;
    }

    h1 {
      text-align: center;
      color: #ffffff;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.4px;
      line-height: 40px;
      margin-bottom: 32px;
      
      
      
      
      @include for-normal-layout {
        margin-bottom: 0px;
        text-align: unset;
        color: #ffffff;
        font-size: 40px;
        font-weight: 700;
        letter-spacing: -0.5px;
        line-height: 56px;
      }
    }
  }
}
