.app-hero {
  background: url(../../img/homepage/hero-bg-mobile.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: unset;

  @include for-normal-layout {
    background: url(../../img/homepage/hero-bg-desktop.jpg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 100vh;

  }


  .logo {
    padding-top: 286px;
    display: flex;

    justify-content: center;
    margin-bottom: 36px;
    @include for-normal-layout {
      padding-top: 105px;
      display: flex;
      justify-content: unset;
      margin-bottom: 0px;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: unset;
    @include for-normal-layout {
      height: 80%;
    }

    h2 {
      color: #ffffff;
      font-size: 32px;
      font-weight: 300;
      letter-spacing: -0.4px;
      line-height: 40px;
      margin-bottom: 32px;
      text-align: center;
      @include for-normal-layout {
        color: #ffffff;
        font-size: 64px;
        font-weight: 300;
        letter-spacing: -0.8px;
        line-height: 72px;
        margin-bottom: 36px;
        text-align: unset;
      }
    }

    p {
      color: #ffffff;
      font-size: 15px;
      font-weight: 400;
      letter-spacing: -0.19px;
      line-height: 25px;
      margin-bottom: 63px;
      text-align: center;
      br{
        display: none;
      }
      @include for-normal-layout {
        text-align: unset;
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        letter-spacing: -0.22px;
        line-height: 30px;
        margin-bottom: 50px;
        br{
          display: block;
          
        }
      }
    }
    .btn-holder {
      
      display: flex;
      justify-content: center;
      padding-bottom: 140px;
      @include for-normal-layout {
        padding-bottom: 0px;
        display: unset;
      }
    }
  }
}









