.app-family {
  height: unset;
  // display: unset;
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  @include for-normal-layout {
    margin-top: 0px;
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    height: 100vh;
  }

  .image-holder {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .image {
      background: url(../../img/homepage/family-gathering-mobile.jpg);
      width: 326px;
      height: 400px;
      margin-bottom: 48px;

      @include for-normal-layout {
        margin-bottom: 0px;
        background: url(../../img/homepage/family-gathering-desktop.jpg);
        width: 540px;
        height: 600px;
      }

      .pattern-one {
        display: none;

        @include for-normal-layout {
          display: block;
          position: relative;
          top: -168px;
          right: 523px;
        }
      }
      .pattern-two {
        display: none;

        @include for-normal-layout {
          display: block;
          position: relative;
          left: -41px;
          top: -368px;
        }
      }
    }
  }
  .content {
    display: grid;
    grid-template-areas:
      "info"
      "big-txt"
      "para"
      "btn";

    @include for-normal-layout {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    h1 {
      text-align: center;
      color: #242b37;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.4px;
      line-height: 40px;
      margin-bottom: 16px;
      grid-area: big-txt;
      @include for-normal-layout {
        text-align: unset;

        grid-area: unset;
        color: #242b37;
        font-size: 40px;
        font-weight: 700;
        letter-spacing: -0.5px;
        line-height: 56px;
        margin-bottom: 16px;
      }
    }
    p {
      text-align: center;
      color: #242b37;
      font-size: 15px;
      font-weight: 400;
      letter-spacing: -0.19px;
      line-height: 25px;
      margin-bottom: 36px;
      grid-area: para;
      br {
        display: none;
      }


      @include for-normal-layout {
        text-align: unset;
        grid-area: unset;
        color: #242b37;
        font-size: 18px;
        font-weight: 400;
        letter-spacing: -0.22px;
        line-height: 30px;
        margin-bottom: 67px;
        br {
          display: block;
        }
      }
    }
    .btn-holder {
      display: flex;
      justify-content: center;
      margin-bottom: 124px;
      
      @include for-normal-layout {
        margin-bottom: 0px;
        
        display: unset;
      }

      button {
        grid-area: btn;

        @include for-normal-layout {
          grid-area: unset;
          margin-bottom: 78px;
        }
      }
    }

    .info-txt {
      grid-area: info;

      @include for-normal-layout {
        grid-area: unset;
      }

      .txt1 {
        text-align: center;
        color: #4c4c4c;
        font-family: spartan-semiBold;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 2px;
        line-height: 28px;
        margin-bottom: 12px;
        @include for-normal-layout {
          text-align: unset;
          color: #4c4c4c;
          font-family: spartan-semiBold;
          font-size: 14px;
          font-weight: 600;
          letter-spacing: 2px;
          line-height: 28px;
          margin-bottom: 12px;
        }
      }
      .txt2 {
        text-align: center;
        opacity: 0.5;
        color: #4c4c4c;
        font-family: spartan-semiBold;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 2px;
        line-height: 28px;
        margin-bottom: 12px;

        @include for-normal-layout {
          text-align: unset;
          opacity: 0.5;
          color: #4c4c4c;
          font-family: spartan-semiBold;
          font-size: 14px;
          font-weight: 600;
          letter-spacing: 2px;
          line-height: 28px;
          margin-bottom: 12px;
        }
      }
      .txt3 {
        text-align: center;
        opacity: 0.5;
        color: #4c4c4c;
        font-family: spartan-semiBold;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 2px;
        line-height: 28px;
        margin-bottom: 24px;

        @include for-normal-layout {
          margin-bottom: 0px;
          text-align: unset;
          opacity: 0.5;
          color: #4c4c4c;
          font-family: spartan-semiBold;
          font-size: 14px;
          font-weight: 600;
          letter-spacing: 2px;
          line-height: 28px;
        }
      }
    }
  }
}
