.app-footer {
  height: 440px;
  background-color: #111111;
  @include for-normal-layout {
    height: 240px;
  }
  .content {
    height: 100%;
    flex-direction: column;
    justify-content: center;
    @include for-normal-layout {
      justify-content: space-between;
      flex-direction: row;
    }
    .logo {
      margin-bottom: 40px;
      @include for-normal-layout {
        margin-bottom: 0px;
      }
    }

    .txt1 {
      width: unset;
      margin-bottom: 40px;
      
      @include for-normal-layout {
        margin-bottom: 0px;
        width: 244px;

      }
      p {
        text-align: center;
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 2px;
        line-height: 28px;
        
        @include for-normal-layout {
          text-align: unset;
          color: #ffffff;
          font-size: 14px;
          font-weight: 400;
          letter-spacing: 2px;
          line-height: 28px;
        }
      }
    }
    .txt2 {
      width: unset;
      @include for-normal-layout {
        width: 446px;
      }
      p {
        
        text-align: center;
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 2px;
        line-height: 28px;
        @include for-normal-layout {
          text-align: unset;
          color: #ffffff;
          font-size: 14px;
          font-weight: 400;
          letter-spacing: 2px;
          line-height: 28px;
        }
      }
    }
  }
}
