.appHeader-nav-color {
  background-color: rgb(200, 228, 228);
  .appHeader-nav {
    font-family: "montserrat";
    h1 {
      margin: 0px;
      font-size: rem(50px);
    }

    .appHeader-menu-btn {
      padding: 0px;
      border: none;
      background-color: unset;
      font-size: 24px;
      outline: none;
      @include for-narrow-layout {
        display: block;

      }
      @include for-normal-layout {
        display: none;
      }
    }

    .appHeader-nav-links {
      position: fixed;
      right: 0;
      top: 0;
      height: 100vh;
      background-color: rgba(240, 255, 255, 0.795);
      margin: 0;
      flex-direction: column;
      align-items: unset;

      justify-content: unset;
      border-left: 1px solid #f1f1f1;
      padding: 0px;
      transform: translateX(100%);
      transition: transform 0.6s;

      li {
        border-bottom: 1px solid #bbbbbb;
      }

      // li:not(:last-child){
      //   border-bottom: 1px solid gainsboro;

      // }
      a {
        text-align: center;
        padding: 14px 0px;

        font-size: rem(22px);
        margin-left: 0px;
        color: $clr1;
        text-decoration: none;
        width: 260px;
        display: block;

        &:hover {
          color: $clr4;
        }
      }

      @include for-narrow-layout {

      }
      @include for-normal-layout {
        transform:none;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        right: unset;
        top: unset;
        position: unset;
        background-color: unset;
        height: unset;

        border: none;
        a {
          padding: 0px;
          margin-left: 50px;
          text-align: unset;
          width: unset;
        }
        li {
          border: none;
        }
      }
    }
    .open-nav-links {
      transform: translateX(0);
      
    }
  }
}

.appHeader-screen {
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background-color: rgba(0, 0, 0, 0.692);
  // display: none;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;

  transition: opacity 0.4s;
}

.show-screen {
  // display: block;
  visibility: visible;
  opacity: 1;

}
