.app-hl {
  height: 1720px;
  background-color: #111111;
  @include for-normal-layout {
    height: 761px;
  }
  .content {
    height: 100%;

    .content-box {
      height: 100%;
      justify-content: center;
      padding-top: 72px;
      @include for-normal-layout {
        padding-top: 190px;
      }

      .icon {
        margin-bottom: 32px;
        display: flex;
        justify-content: center;

        @include for-normal-layout {
          margin-bottom: 48px;
          display: flex;
          justify-content: unset;
        }
      }
      .box-container {
        display: unset;
        @include for-normal-layout {
          display: flex;
        }

        .box1 {
          width: 100%;
          @include for-normal-layout {
            width: 50%;
          }

          h1 {
            text-align: center;
            color: #ffffff;
            font-size: 32px;
            font-weight: 700;
            letter-spacing: -0.4px;
            line-height: 40px;
            margin-bottom: 24px;
            br {
              display: none;
            }

            @include for-normal-layout {
              color: #ffffff;
              font-size: 40px;
              font-weight: 700;
              letter-spacing: -0.5px;
              line-height: 56px;
              margin-bottom: 29px;
              text-align: unset;
              br {
                display: unset;
              }
            }
          }

          p {
            text-align: center;
            color: #ffffff;
            font-size: 15px;
            font-weight: 400;
            letter-spacing: -0.19px;
            line-height: 25px;
            margin-bottom: 92px;
            br {
              display: none;
            }
            
            
            @include for-normal-layout {
              text-align: unset;
              color: #ffffff;
              font-size: 18px;
              font-weight: 400;
              letter-spacing: -0.22px;
              line-height: 30px;
              margin-bottom: 0px;
              br {
                display: unset;
              }
            }
          }
        }
        .box2 {
        }
      }
    }
  }
}
