.item-preview {
  margin-bottom: 24px;
  display: unset;
  @include for-normal-layout {
    display: flex;
  }

  .image {
    margin-right: 0px;
    margin-bottom: 32px;
    display: flex;
    justify-content: center;
    
    @include for-normal-layout {
      margin-bottom: 0px;
      margin-right: 62px;
      display: unset;

    }

    .icon-two {
      margin-bottom: 48px;
      position: absolute;
      margin-left: 113px;
      display: none;

      @include for-normal-layout {
        display: block;
      }
    }
    img {
      width: 327px;
      height: 245px;

      @include for-normal-layout {
        width: 128px;
        height: 96px;
      }
    }
  }
  h1 {
    text-align: center;
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: -0.22px;
    line-height: 28px;
    margin-bottom: 4px;

    @include for-normal-layout {
      text-align: unset;
      color: #ffffff;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: -0.22px;
      line-height: 28px;
      margin-bottom: 4px;
    }
  }
  .txt{
    margin-bottom: 32px;
    @include for-normal-layout {
      margin-bottom: 0px;
    }
    

    p {
      text-align: center;
      color: #ffffff;
      font-size: 15px;
      font-weight: 400;
      letter-spacing: -0.19px;
      line-height: 28px;
      // margin-bottom: 57px;
      
      
      @include for-normal-layout {
        text-align: unset;
        
        color: #ffffff;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: -0.19px;
        line-height: 28px;
        // margin-bottom: 0px;

      }
    }
  }
}
