.app-info {
  .contant-one {
    display: unset;
    justify-content: unset;

    @include for-normal-layout {
      display: flex;
      justify-content: space-between;
    }
    .image {
      position: relative;
      top: -66px;
      background: url(../../img/homepage/enjoyable-place-mobile.jpg);
      background-repeat: no-repeat;
      width: unset;
      height: 400px;
      background-size: 100% 100%;
      box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);
      -webkit-box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);
      -moz-box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);

      @include for-normal-layout {
        position: relative;
        top: -66px;
        background: url(../../img/homepage/enjoyable-place-desktop.jpg);
        background-repeat: no-repeat;
        width: 540px;
        height: 720px;
        box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);
        -webkit-box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);
        -moz-box-shadow: -1px 12px 27px -1px rgba(0, 0, 0, 0.58);
      }

      .pattern {
        display: none;

        @include for-normal-layout {
          display: block;
          position: relative;
          top: 368px;
          right: 31px;
        }
      }
    }
    .box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .icon {
        display: flex;
        justify-content: center;
        margin-bottom: 32px;
        @include for-normal-layout {
          display: unset;
          margin-bottom: 48px;
        }
      }

      h1 {
        color: #242b37;
        font-size: 32px;
        font-weight: 700;
        letter-spacing: -0.4px;
        line-height: 40px;
        text-align: center;
        margin-bottom: 18px;

        br{
          display: none;
        }

        @include for-normal-layout {
          text-align: unset;
          color: #242b37;
          font-size: 40px;
          font-weight: 700;
          letter-spacing: -0.5px;
          line-height: 56px;
          margin-bottom: 21px;
          br{
            display: block;
          }
        }
      }
      p {
        color: #242b37;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: -0.19px;
        line-height: 25px;
        text-align: center;
        margin-bottom: 108px;
        br{
          display: none;
        }

        @include for-normal-layout {
          text-align: unset;
          color: #242b37;
          font-size: 18px;
          font-weight: 400;
          letter-spacing: -0.22px;
          line-height: 30px;
          margin-bottom: 0;
          br{
            display: block;
          }
          
        }
      }
    }
  }
  .contant-two {
    flex-direction: column-reverse;
    @include for-normal-layout {
      flex-direction: unset;
    }
    .image {
      position: unset;
      top: unset;

      background: url(../../img/homepage/locally-sourced-mobile.jpg);
      background-repeat: no-repeat;
      width: unset;
      height: 400px;
      background-size: 100% 100%;

      box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);
      -webkit-box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);
      -moz-box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);

      @include for-normal-layout {
        position: relative;
        top: 66px;
        background: url(../../img/homepage/locally-sourced-desktop.jpg);
        width: 540px;
        height: 720px;
        box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);
        -webkit-box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);
        -moz-box-shadow: -1px 12px 27px -1px rgba(74, 74, 74, 0.58);
      }
      .pattern {
        display: none;
        @include for-normal-layout {
          position: relative;
          top: 333px;
          right: 355px;
          display: block;
        }
      }
      .pattern-three {
        display: none;
        @include for-normal-layout {
          position: absolute;
          top: 282px;
          left: 492px;
          display: block;
        }
      }
    }
    .box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      position: unset;
      top: unset;

      @include for-normal-layout {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 50%;
        position: relative;
        top: 66px;
      }
      .icon {
        margin-bottom: 48px;
        display: flex;
        justify-content: center;
        margin-top: 32px;
        @include for-normal-layout {
          margin-bottom: 48px;
          display: unset;
          justify-content: unset;
          margin-top: 48px;
        }
      }

      h1 {
        color: #242b37;
        font-size: 32px;
        font-weight: 700;
        letter-spacing: -0.4px;
        line-height: 40px;
        text-align: center;
        margin-bottom: 24px;

        br{
          display: none;
        }
        
        @include for-normal-layout {
          text-align: unset;
          color: #242b37;
          font-size: 40px;
          font-weight: 700;
          letter-spacing: -0.5px;
          line-height: 56px;
          margin-bottom: 21px;
          br{
            display: block;
          }
        }
      }
      p {
        color: #242b37;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: -0.19px;
        line-height: 25px;
        text-align: center;
        margin-bottom: 109px;

        br{
          display: none;
        }
        
        
        @include for-normal-layout {
          text-align: unset;
          color: #242b37;
          font-size: 18px;
          font-weight: 400;
          letter-spacing: -0.22px;
          line-height: 30px;
          margin-bottom: 0px;
          br{
            display: block;
          }

        }
      }
    }
  }
}
